.wholeTable {
	width: 50%;
	margin: 0 auto;
}

div {
	background-color: #191937;
}

.input {
	text-align: center;
	padding: 16px;
}

input {
	width: 25%;
	background-color: #9f9fb4;
}

input::placeholder {
	color: black;
}

.bitcoin-container,
.ethereum-container,
.tether-container,
.bnb.container {
	position: absolute;
	height: 0px;
	width: 0px;
}

.bitcoin {
	display: inline;
	height: 200px;
	width: 200px;
	position: fixed;
	top: 150px;
	left: 125px;
	animation: animacija infinite 0.8s alternate ease-in-out;
}

.ethereum {
	display: inline;
	height: 200px;
	width: 200px;
	position: fixed;
	top: 480px;
	left: 125px;
	animation: animacija infinite 0.8s alternate ease-in-out;
}

.tether {
	display: inline;
	height: 200px;
	width: 200px;
	position: fixed;
	top: 150px;
	right: 125px;
	animation: animacija infinite 0.8s alternate ease-in-out;
}

.bnb {
	display: inline;
	height: 200px;
	width: 200px;
	position: fixed;
	top: 480px;
	right: 125px;
	animation: animacija infinite 0.8s alternate ease-in-out;
}

.arrow-down {
	background-color: transparent;
	border: none;
	padding: 5px 10px;
	line-height: 1;
	outline: none !important;
	box-shadow: none !important;
}

.arrow-up {
	outline: none !important;
	box-shadow: none !important;
	transform: rotate(180deg);
}

@keyframes animacija {
	from {
		transform: translateY(0);
	}
	to {
		transform: translateY(100px);
	}
}

.back-to-top {
	position: fixed;
	bottom: 20px;
	right: 20px;
	font-size: 100px;
	background: orange;
	color: white;
	cursor: pointer;
	border-radius: 100px;
	border: none;
	box-shadow: 0 5px 10px #ccc;
}

.back-to-top:hover {
	background: red;
}

.buttonStyle {
	display: flex;
	justify-content: center;
	align-items: center;
	position: fixed;
	background-color: rgb(10, 14, 21);
	border-radius: 8px;
	cursor: pointer;
	color: white;
	height: 42px;
	width: 42px;
	bottom: 50px;
	right: 200px;
}

@media (max-width: 1024px) {
	.name {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
	}
}

@media (max-width: 768px) {
	.wholeTable {
		width: 100%;
	}

	.table {
		width: 80%;
		margin: 0 auto;
	}

	.name {
		display: block;
		padding: 0;
		border: none;
	}

	input {
		width: 50%;
	}

	.buttonStyle {
		right: 20px;
	}
}

@media (max-width: 425px) {
	.wholeTable {
		width: 100%;
	}

	.name {
		display: flex;
		justify-content: center;
		align-items: center;
		flex-direction: column;
		border: none;
	}

	.history {
		display: none;
	}

	input {
		width: 80%;
	}

	.buttonStyle {
		width: 35px;
		height: 35px;
		right: 15px;
	}
}

@media (max-width: 320px) {
	.number {
		display: none;
	}

	.history {
		display: none;
	}

	input {
		width: 100%;
	}

	.buttonStyle {
		width: 35px;
		height: 35px;
		right: 15px;
	}

	.arrowPrice {
		margin-left: 32px;
	}
}
